import React from 'react';

import './App.css';
import { BrowserRouter as Router, NavLink} from 'react-router-dom';
import {Route} from 'react-router-dom';
import Form from './Form/Form';
import ProductList from './ProductList/ProductList';
import ProductDes from './ProductDes/ProductDes';

function App() {
  return (
    <React.Fragment>
         <Router>
           <div className="navWrapper">
             <div className="logoWrapper">
                <span className="lowerLogo">insta</span><span className="upperLogo">BUY</span>
             </div>
             <div className="linkWrapper">
                <NavLink exact to="/" className="linkStyle" activeClassName="linkActiveStyle"> Add Product</NavLink>
                <NavLink to="/productlist" className="linkStyle" activeClassName="linkActiveStyle">View All</NavLink>
                <NavLink to="/product1" className="linkStyle" activeClassName="linkActiveStyle">Product</NavLink>
             </div>
           </div>
                <Route exact path="/" component={Form}></Route>
                <Route path="/productlist" component={ProductList}></Route>
                <Route path="/product1" component={ProductDes}></Route>
         </Router>
    </React.Fragment>
  );
}

export default App;
