import React, { useState } from 'react';

import './Form.css';
const logo = require('../assets/Group 24.png');

const Form = () => {


    const [divclassS,setdivclassS] = useState('sizeBox');
    const [divclassM,setdivclassM] = useState('sizeBox');
    const [divclassL,setdivclassL] = useState('sizeBox');
    const [divclassXL,setdivclassXL] = useState('sizeBox');
    const [divclassXXL,setdivclassXXL] = useState('sizeBox');

    const onsizeSHandler = () => {
        if(divclassS == 'sizeBox')
            setdivclassS('sizeBox-active');
        else
            setdivclassS('sizeBox')
    };

    const onsizeMHandler = () => {
        if(divclassM == 'sizeBox')
            setdivclassM('sizeBox-active');
        else
            setdivclassM('sizeBox')
    };

    const onsizeLHandler = () => {
        if(divclassL == 'sizeBox')
            setdivclassL('sizeBox-active');
        else
            setdivclassL('sizeBox')
    };

    const onsizeXLHandler = () => {
        if(divclassXL == 'sizeBox')
            setdivclassXL('sizeBox-active');
        else
            setdivclassXL('sizeBox')
    };

    const onsizeXXLHandler = () => {
        if(divclassXXL == 'sizeBox')
            setdivclassXXL('sizeBox-active');
        else
            setdivclassXXL('sizeBox')
    };




    return <div className="formDiv">
                <section className="formSection">
                    <div className="logoContainer">
                        <img src={logo} atl="logo" className="logoImg"></img>
                    </div>
                    <div className="formContainer">
                            <div className="formWrapper">
                                <form  enctype="multipart/form-data">
                                        <input type="text" placeholder="Product ID"></input><br></br>
                                        <input type="text" placeholder="Product Name"></input><br></br>
                                        <input type="text" placeholder="Price"></input>
                                   
                                       <div className="gen-row">
                                        <div className="genderCol">
                                            <input type="radio" id="men" name="gender" value="Men"/>
                                            <label for="men" className="genderLabel">Men</label>
                                        </div>
                                        <div className="genderCol">
                                            <input type="radio" id="women" name="gender" value="Women"/>
                                            <label for="women" className="genderLabel">Women</label>
                                        </div>
                                        <div className="genderCol">
                                            <input type="radio" id="boys" name="gender" value="Boys"/>
                                            <label for="boys" className="genderLabel">Boys</label>
                                        </div>
                                        <div className="genderCol">
                                            <input type="radio" id="girls" name="gender" value="Girls"/>
                                            <label for="girls" className="genderLabel">Girls</label>
                                        </div>
                                    </div>

                                    
                                        <div>
                                                <select className="select-brand" >
                                                    <option value="0">Brand</option>
                                                    <option value="volvo">Adidas</option>
                                                    <option value="saab">Roadster</option>
                                                </select>
                                                <select className="select-category" > 
                                                    <option value="0">Category</option>
                                                    <option value="mercedes">Shoes</option>
                                                    <option value="audi">T-shirt</option>
                                                </select>
                                        </div>
                                        <div>
                                                <select className="select-color" >
                                                    <option value="0">Select Color</option>
                                                    <option value="1">Blue</option>
                                                    <option value="2">Red</option>
                                                    <option value="3">Yellow</option>
                                                    <option value="4">Purple</option>
                                                    <option value="5">Teal</option>
                                                    <option value="6">Pink</option>
                                                    <option value="7">Magenta</option>
                                                    <option value="8">Black</option>
                                                    <option value="9">White</option>
                                                </select>
                                     </div>

                                     <div className="sizeWrapper">
                                         
                                         <span className="sizeLabel">Available Sizes</span>
                                         <div className="sizeBoxWrapper">
                                            <div className={divclassS} onClick={onsizeSHandler}>S</div>
                                            <div className={divclassM} onClick={onsizeMHandler}>M</div>
                                            <div className={divclassL} onClick={onsizeLHandler}>L</div>
                                            <div className={divclassXL} onClick={onsizeXLHandler}>XL</div>
                                            <div className={divclassXXL} onClick={onsizeXXLHandler}>XXL</div>
                                         </div>
                                      </div>      
                                    
                                     <div className="fileUpload">
                                        <input type= "file" placeholder="No file selected ..." id="lname" name="lname"  className="fileName"/>
                                     </div>
                                      <button className="submitBut">Add Product</button>
                        
                                </form>

                            </div>
                    </div>
                </section>
           </div>
};

export default Form;